import { EventForm } from "./event-form"
import { HTTP, submitEventTask, updateEventTask } from "Http"
import { validateTask } from "./validations"
import { Animate, slideInDown, slideOutUp } from "Styles"

export const Editor = ({ attrs: { mdl } }) => {
  const EventFormData = (mdl) =>
    mdl.Events.editEvent()
      ? {
          shortDate: mdl.Events.editEvent().date,
          allDay: mdl.Events.editEvent().allDay,
          inPerson: mdl.Events.editEvent().inPerson,
          location: mdl.Events.editEvent().location,
          latlong: mdl.Events.editEvent().latlong,
          startTime: M(mdl.Events.editEvent().start).format("HH:mm"),
          endTime: M(mdl.Events.editEvent().end).format("HH:mm"),
          title: mdl.Events.editEvent().title,
          notes: mdl.Events.editEvent().notes,
        }
      : {
          shortDate: mdl.selectedDate().format("YYYY-MM-DD"),
          allDay: false,
          inPerson: true,
          location: "",
          latlong: "",
          startTime: "",
          endTime: "",
          title: "",
          notes: "",
        }

  let EventFormState = {
    status: "loading",
    errors: null,
    isSubmitted: false,
    isValid: false,
    queryResults: [],
    querySelected: "",
    locationWarning: Stream(null),
  }

  const resetState = (state) => {
    EventFormState = {
      status: "loading",
      errors: null,
      isSubmitted: false,
      isValid: false,
      queryResults: [],
      querySelected: "",
      locationWarning: Stream(null),
    }
  }

  const validate = (state, data) => {
    const onError = (errors) => {
      state.errors = errors
      state.isValid = false
      // console.log("v err", state, errors)
    }

    const onSuccess = (data) => {
      // console.log("v succ", data)
      state.errors = null
      state.isValid = true
    }

    validateTask(data).fork(onError, onSuccess)
  }

  const submit = ({ mdl, data, state }) =>
    mdl.Events.editEvent()
      ? editEvent({ mdl, data, state })
      : addNewEvent({ mdl, data, state })

  const addNewEvent = ({ mdl, data, state }) => {
    const onError = (errors) => {
      state.errors = errors
      state.status = "failed"
    }

    const onSuccess = () => {
      mdl.Invites.fetch(true)
      mdl.Events.createNewEvent(false)
    }

    state.isSubmitted = true
    validateTask(data)
      .chain(submitEventTask(HTTP)(mdl))
      .fork(onError, onSuccess)
  }

  const editEvent = ({ mdl, data, state }) => {
    const onError = (errors) => {
      state.errors = errors
      state.status = "failed"
    }

    const onSuccess = (d) => {
      console.log("success upddtae event", d)
      mdl.Invites.fetch(true)
      mdl.Events.fetch(true)
      mdl.Events.editEvent(null)
    }

    state.isSubmitted = true
    validateTask(data)
      .chain(updateEventTask(HTTP)(mdl))
      .fork(onError, onSuccess)
  }

  return {
    view: ({ attrs: { mdl } }) =>
      m(EventForm, {
        oncreate: Animate(slideInDown, { delay: 2 }),
        onbeforeremove: Animate(slideOutUp, { delay: 2 }),
        mdl,
        data: EventFormData(mdl),
        state: EventFormState,
        validate,
        resetState,
        submit,
      }),
  }
}
